export default "import React from 'react';\r\nimport Input from './input';\r\n\r\nconst InputExample = () => {\r\n  return (\r\n    <div>\r\n      <h1>Input组件</h1>\r\n      <br/>\r\n\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"placeholder\"\r\n      />\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"disabled input\"\r\n        disabled\r\n      />\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"input with icon\"\r\n        icon=\"search\"\r\n      />\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"large size\"\r\n        size=\"lg\"\r\n      />\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"small size\"\r\n        size=\"sm\"\r\n      />\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"prepend text\"\r\n        prepend=\"https://\"\r\n      />\r\n      <Input\r\n        style={{width: '300px'}}\r\n        placeholder=\"google\"\r\n        append=\".com\"\r\n      />\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default InputExample;";